import {__} from '@wordpress/i18n';
import {
    PanelBody,
    BaseControl,
    Dashicon,
    Button,
    TextControl,
    ToggleControl,
    TextareaControl,
    Tip
}           from '@wordpress/components';
import {
    MediaUploadCheck,
    MediaUpload,
    RichText,
    useBlockProps,
    InspectorControls
}           from '@wordpress/block-editor';

export default function Edit(props) {
    const {attributes, setAttributes} = props;
    const {
        logo,
        logoURL,
        logoAlt,
        logoH,
        logoW,
        heading,
        price,
        salePrice,
        link,
        buttonLabel,
        isInternal,
        isSponsored
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    function setMediaAttribute(name, value) {
        setAttributes({[name]: value.url});
        setAttributes({[name + 'Alt']: value.alt});
        setAttributes({[name + 'H']: value.height});
        setAttributes({[name + 'W']: value.width});
    }

    function removeMediaAttribute(name) {
        setAttributes({[name]: ''});
        setAttributes({[name + 'Alt']: ''});
        setAttributes({[name + 'H']: null});
        setAttributes({[name + 'W']: null});
    }

    const blockProps = useBlockProps({
        className: `af-list-item`,
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('Details')} initialOpen={true}>
                    <BaseControl>
                        <Tip>{__('You can control styling for all Affiliate List Items in the parent (Affiliate List) block.')}</Tip>
                    </BaseControl>
                    <TextControl
                        label={__('Brand Name')}
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        placeholder={__('Enter brand name...')}
                    />
                    <TextareaControl
                        label={__('Add Custom Logo URL')}
                        value={logoURL}
                        onChange={(value) => setAttribute('logoURL', value)}
                        help={__('You can add logo or input custom image URL here.')}
                        placeholder={__('//website.com/.../image.jpg')}
                    />
                    <TextControl
                        label={__('Price')}
                        placeholder="99"
                        value={price}
                        onChange={(value) => setAttribute('price', value)}
                    />
                    <TextControl
                        label={__('Discounted Price')}
                        placeholder="$49"
                        value={salePrice}
                        onChange={(value) => setAttribute('salePrice', value)}
                    />
                    <TextControl
                        label={__('Affiliate Link')}
                        placeholder={__('https://...')}
                        help={__('Input the full link, including the HTTPS protocol')}
                        value={link}
                        onChange={(value) => setAttribute('link', value)}
                    />
                    <TextControl
                        label={__('Button Label')}
                        value={buttonLabel}
                        onChange={(value) => setAttribute('buttonLabel', value)}
                    />
                    <ToggleControl
                        label={__('Internal Link?')}
                        checked={isInternal}
                        onChange={(value) => setAttribute('isInternal', value)}
                        help={__('Disable opening in a new tab and remove the nofollow,sponsored attributes in the rel for internal links.')}
                    />
                    <ToggleControl
                        label={__('Sponsored?')}
                        checked={isSponsored}
                        onChange={(value) => setAttribute('isSponsored', value)}
                        help={__('Tell the search engine bot your relationship with the linked page.')}
                    />
                </PanelBody>
            </InspectorControls>
            <div className="af-list-item-inner">
                <div className="af-list-item-left">
                    <div>
                        {(logoURL && !logo) && (
                            <img className="af-list-logo" src={logoURL} alt="Download Image"/>
                        )}
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setMediaAttribute('logo', value)}
                                allowedTypes={['image']}
                                value={logo}
                                render={({open}) => (
                                    <div>
                                        {!logo && (
                                            <Button className="rb-editor-media-add" onClick={open}>
                                                <Dashicon icon="plus-alt2"/>
                                                {__('Add Logo')}
                                            </Button>
                                        )}
                                        {logo && (
                                            <div className="rb-editor-media-preview">
                                                <img className="af-list-logo" src={logo} alt={logoAlt || 'logo'}/>
                                                <Button className="rb-editor-media-remove" onClick={() => removeMediaAttribute('logo')}>
                                                    <Dashicon icon="minus"/>
                                                    {__('Remove Logo')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </div>
                    <RichText
                        tagName="span"
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        className="gb-small-heading h4"
                        placeholder={__('Enter brand name...')}
                    />
                </div>
                <div className="af-list-item-right">
                    {salePrice ? (
                        <span className="af-price h4"><del>{price}</del>
                            {salePrice}</span>
                    ) : (
                        <span className="af-price h4">{price}</span>
                    )}
                    {buttonLabel && (
                        <span className="af-button is-btn">{buttonLabel} </span>
                    )}
                </div>
            </div>
        </div>
    );
}
