import {RichText, useBlockProps} from '@wordpress/block-editor';

export default function save({attributes}) {
    const {
        logo,
        logoURL,
        logoAlt,
        logoH,
        logoW,
        heading,
        price,
        salePrice,
        link,
        buttonLabel,
        isInternal,
        isSponsored
    } = attributes;

    const blockProps = useBlockProps.save({
        className: 'af-list-item',
    });

    return (
        <div {...blockProps}>
            <div className="af-list-item-inner">
                <div className="af-list-item-left">
                    {logo ? (
                        <img loading="lazy" className="af-list-logo" src={logo} height={logoH || ""} width={logoW || ""} alt={logoAlt || ""}/>
                    ) : logoURL ? (
                        <img loading="lazy" className="af-list-logo" src={logoURL} alt=""/>
                    ) : null}
                    {heading && (
                        <RichText.Content
                            tagName="span"
                            className="gb-small-heading h4"
                            value={heading}
                        />
                    )}
                </div>
                <div className="af-list-item-right">
                    {salePrice ? (
                        <span className="af-price h4"><del>{price}</del>
                            {salePrice}</span>
                    ) : (
                        <span className="af-price h4">{price}</span>
                    )}
                    {buttonLabel && (
                        <a
                            href={link}
                            target={isInternal ? null : "_blank"}
                            rel={isInternal ? null : (isSponsored ? "sponsored noopener" : "noopener noreferrer nofollow")}
                            className="af-button is-btn">
                            {buttonLabel}
                        </a>
                    )}
                </div>
            </div>
        </div>
    );
}
